/*
antd样式覆盖
*/

@import './const.css';

.ant-layout-header {
    background-color: white;
}

.ant-table-title {
    color:var(--title-color);
    font-weight: 500;
    padding: 16px;
}

.ant-collapse-header {
    background-color: var(--main-color);
    font-weight: 500;
    color: var(--title-color) !important;
}

.ant-layout-footer a {
    color: inherit;
}